<template>
	<div class="user_wrap">
		<el-row :gutter="10" class="user-top">
			<el-col :span = "5">
				<el-input placeholder="请输入用户名" prefix-icon="el-icon-search" v-model="searchData.input1"></el-input>
			</el-col>
			
			<el-col :span = "5">
				<el-date-picker
			      	type="daterange"
			      	v-model="searchData.startDate_endData"
			      	start-placeholder="开始时间"
	      		  	end-placeholder="结束时间"
	      		  	value-format="yyyy-MM-dd"
	      		  	style="width:250px">
			    </el-date-picker>
			</el-col>
			<el-col :span= "5">
				<el-select v-model="searchData.role" placeholder="请选择角色" style="width: 100%;" >
				    <el-option
				        v-for="item in options"
				        :key="item.ROLE_ID"
				        :label="item.ROLE_NAME"
				        :value="item.ROLE_ID">
				    </el-option>
			    </el-select>
			</el-col>
			<el-col :span="5" >
				<el-button icon="el-icon-search" @click = "init"></el-button>
				<el-button type = "primary">批量导出</el-button>
			</el-col>
		</el-row>
	<!--table-->
		<div  style="margin-top:10px;padding: 20px;background: #FFFFFF;">
			<el-row style="padding-bottom: 20px; ">
				<el-col :span="6" :offset="18">
					<router-link :to="{path:'/addEditUser_list'}">
						<el-button type="success">新增人员</el-button>
					</router-link>
					<el-button @click = "allDelBut" :disabled="this.multipleSelection.length == 0"> 批量删除</el-button>
				</el-col>
				
			</el-row>
			<!--删除弹框-->
				<el-dialog
				    :visible.sync="delDialogObj.deldialogVisible" width="30%":before-close="handleClose">
				    <div style="text-align: center;">
				    	<img src="../../assets/del-image/del_image.png"/>
				    	<h3>{{delDialogObj.title}}</h3>
				    	<span>{{delDialogObj.content}}</span>
				    </div>
				    <span slot="footer" class="dialog-footer dialog-footer_span">
					    <el-button @click="delDialogObj.deldialogVisible = false">我在想想</el-button>
					    <el-button type="primary" @click="batchDeleting" v-if="delDialogObj.isAll">确定</el-button>
					    <el-button type="primary" @click="oneDeleteing" v-else>确定</el-button>
				    </span>
				</el-dialog>
				
			<el-row>
				<el-col>
					<el-table ref="multipleTable"  
						:data="tableData3"
						border
					    style="width: 100%"
					    @selection-change="handleSelectionChange">
					    <el-table-column  type="selection" width="55">
					    </el-table-column>
					    
					    <el-table-column  prop="USER_ID" label="编号"  width="120">
					    </el-table-column>
					    <el-table-column prop="USERNAME" label="用户名"   width="120">
					    </el-table-column>
					    <el-table-column prop="UNAME" label="姓名"   show-overflow-tooltip>
					    </el-table-column>
					    <el-table-column prop="ROLE_ID" label="角色"  show-overflow-tooltip >
					    </el-table-column>
					    <el-table-column prop="EMAIL" label="邮箱"  width="180" show-overflow-tooltip>
					    </el-table-column>
					    <el-table-column prop="LAST_LOGIN" label="最近登录" show-overflow-tooltip >
					    </el-table-column>
					    <el-table-column prop="LAST_IP" label="上次登录IP"  show-overflow-tooltip >
					    </el-table-column>
					    <el-table-column  label="操作" width="300">
					    	<template slot-scope="scope">
				     			<el-button size="mini" @click="bundlingFun({id:scope.row.USER_ID,name:scope.row.USERNAME})">BO绑定</el-button>
				     			<el-button size="mini" >岗位</el-button>
					    		<el-button type="info" icon="el-icon-edit" size="mini" @click="editUserFun({id:scope.row.USER_ID})"></el-button>
				     			<el-button type="danger" icon="el-icon-delete" size="mini" @click="delFun({id:scope.row.USER_ID})"></el-button>

					    	</template>
					    </el-table-column>
					</el-table>
				<!--BO绑定-->
					<el-dialog
					    title="BO绑定"
					    :visible.sync="bindingDialogVisible"
					    width="80%"
					    :before-close="handleClose">
					    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
					    	<el-form-item label="Bokey：" >
						    	<el-row :gutter ="20">
						    		<el-col :span="10">
						    			<el-autocomplete
											   v-model="ruleForm.BO_KEY"
											   :valueKey ="valueKey"
											   style="width: 100%;"
											   :fetch-suggestions="querySearchAsync"
											   placeholder="请输入BO_key值"
											   @select="handleSelect">
										</el-autocomplete>
						    		</el-col>
						    		<el-col :span="2">
						    			<el-button type="primary" icon="el-icon-search" @click="searchHelp">帮助</el-button>
						    		</el-col>
						    	</el-row>
						    </el-form-item>
						    
								<el-table :data="addRightsData" border   style="width: 85%;margin:0 auto">
							        <el-table-column label="BO_KEY" wdith="100">
							        	<template slot-scope="scope">
							        		{{scope.row.BO_KEY}}
							        	</template>
							        </el-table-column>
							        <el-table-column  label="BO_DESC">
							        	<template slot-scope ="scope">
							        		{{scope.row.BO_DESC}}
							        	</template>
							        </el-table-column>
							        <el-table-column  label="BO_VALUE">
							        	<template slot-scope ="scope">
							        		{{scope.row.BO_VALUE}}
							        	</template>
							        </el-table-column>
							        <el-table-column  label="操作" width="80">
							        	<template slot-scope ="scope">
							        		<el-button type ="danger"icon="el-icon-delete" size="mini" @click="addRightsData.splice(scope.$index,1)"></el-button>
							        	</template>
							        </el-table-column>
							    </el-table>
					  	</el-form>
					    <span slot="footer" class="dialog-footer">
						    <el-button type="primary" @click="userJoinBo">确 定</el-button>
					    </span>
					</el-dialog>
					<!--搜索帮助弹框-->
					<el-dialog  title="搜索帮助" :visible.sync="queryHelpdialogVisible"  width="55%" :before-close="handleClose">
					 	<el-table  ref="queryMultipleTable"  :data="queryTableData"  tooltip-effect="dark"  style="width: 100%"
						    @selection-change="queryHelpChange">
						    <el-table-column type="selection"  width="55">
						    </el-table-column>
						    <el-table-column  label="Bokey" prop="BO_KEY" show-overflow-tooltip>
						     
						    </el-table-column>
						    <el-table-column  label="Bodesc" prop="BO_DESC" show-overflow-tooltip>
						    </el-table-column>
						    <el-table-column prop="" label="Bovalue" prop="BO_VALUE" show-overflow-tooltip>
						    </el-table-column>
						  </el-table>
					    <span slot="footer" class="dialog-footer">
					    	<el-button type="primary" @click="addBoFun">确定添加</el-button>
					    </span>
					</el-dialog>
				</el-col>
			</el-row>
			<el-row style="padding-top:20px ;">
				<el-col :span="4" :offset="14">
					<el-pagination 
						@size-change="handleSizeChange"
						@current-change="handleCurrentChange"
			            :current-page="currentPage"
			            :total="organNumber"
			            :page-size="pagesize"		        		
			            layout="total, prev, pager, next, jumper">
					</el-pagination>
				</el-col>
			</el-row>		
		
		
		</div>
	</div>
</template>

<script>
	import userData from './user.js'
	import {
		getUsersPort ,  //系统用户查询
		deletePLUserPost,	//删除用户
		serchBoPost,     //bo数据
		saveUserObjectPost,   //保存用户和BO的关系
		queryBoByUserPost    //用户和BO回显
	} from 'api/systemUser.js'
	import{
		queryRolePort   //查询角色
	}from 'api/authority.js'
	import userByBoTable from '@/components/userByBoTable.vue'
  	export default {
	    name: 'systemUser',
	    components:{
	    	userByBoTable:userByBoTable
	    },
	    data() {
	      	return {
	      		tableData3:userData,
	      		queryHelpdialogVisible:false,  //搜索帮助
				bindingDialogVisible:false,   //bo绑定
	      	    ruleForm:{
		        	BO_KEY:'',
		        	BO_VALUE:''
		        },
		        rules:{
//		        	BO_KEY:[
//		        		{ required: true, message: '请选择key值', trigger: 'change' },
//		            ],
		        },
		        valueKey:'BO_KEY',
		        delId:'',	//删除行id
		        addId:'',//绑定id
		        addName:'',  //用户名
		        rightsData:[],//权限对象
		        addRightsData:[],  //添加
		        queryTableData:[],//搜索帮助数据
	      		//删除弹框
	      		delDialogObj:{
					deldialogVisible:false,
					title:'删除操作',
					content:'删除后不可恢复，确认要删除吗？',
					isAll:false,
				},
				//筛选
		  		searchData:{
		  			input1:'',
		  			startDate_endData:'',
		  			role:''
		  		},
		  		options:[],
		        multipleSelection: [],
		        queryMultipleSelection: [],
		        pagesize:5,
		   		currentPage:1,
		   		organNumber:0,
		   		//bo数据 input
		   		restaurants: [],
		   		timeout:null,
		   		//搜索帮助头部
		   		tableHead:{
		   			head01:'BoKey',
		   			head01:'Bodesc',
		   			head01:'Bovalue',
		   		}
	      	}
	    },
	    computed: {
	    	
	    },
	    created(){
	    	this.init()
	    	this.selectRole()
	    },
	    mounted() {
	        this.restaurants = this.loadAll();
	    },
	    methods:{
	  		//初始化table 
	  		init(){
	  			let resData = {
	  				limit:this.currentPage,    //当前页
	  				offset:this.pagesize ,//条数
					USERNAME:this.searchData.input1,    //登录用户名
					CREATETIME:this.searchData.startDate_endData[0],//开始时间
					LAST_CHANGE:this.searchData.startDate_endData[1],    //结束时间
					ROLE_ID:this.searchData.role 		//角色id
	  			}
//	  			console.log('用户'+JSON.stringify(resData))
	  			getUsersPort(resData).then( res =>{
	  				this.tableData3 = res.ALLUSER
	  				this.organNumber = res.TOTALRESULT
//	  				console.log(JSON.stringify(res))
	  			})
	  		},
	  		//bo绑定input
	  		 loadAll(){
//	  		 	console.log(JSON.parse(window.localStorage.getItem('boData')))
	  		 	return JSON.parse(window.localStorage.getItem('boData'))
	  		 },
	  		//绑定按钮
	  		bundlingFun(obj){
	  			this.addId = obj.id
	  			this.addName = obj.name
	  			this.bindingDialogVisible = true
	  			
	  			//回显user与bo关系
	  			queryBoByUserPost({USER_ID:obj.id}).then( res =>{
	  				this.addRightsData = res.LISTBO
	  				console.log('huixian::'+JSON.stringify(res))
	  			})
	  			serchBoPost().then( res =>{
  					window.localStorage.setItem('boData',JSON.stringify(res.LISTBO))
	  			})
	  		},
	  		//搜索帮助
	  		searchHelp(){
	  			this.queryHelpdialogVisible = true
	  			serchBoPost({BO_KEY:this.ruleForm.BO_KEY}).then( res =>{
	  				this.queryTableData = res.LISTBO
//					console.log('搜索帮助'+JSON.stringify(res))
	  			})
	  		},
	  		//搜索帮助多选
	  		queryHelpChange(val){
	  			this.queryMultipleSelection = val
	  		},
	  		//搜索帮助确认添加
	  		addBoFun(){
	  			for(var item of this.queryMultipleSelection){
	  				this.addRightsData.push({BO_KEY:item.BO_KEY,BO_VALUE:item.BO_VALUE,BO_DESC:item.BO_DESC})
	  			}
	  			if(this.queryMultipleSelection != ''){
	  				this.ruleForm.BO_KEY = null
	  				this.queryHelpdialogVisible = false
	  			}else{
	  				this.$message.warning('请添加权限！')
	  			}
	  		},
	  		//确定user关联Bo
	  		userJoinBo(){
	  			let resData = {
	  				DB_ID:this.addRightsData.map(item => item.BO_ID).join(),
					USER_ID:this.addId,
					BO_ID:'',
					BO_KEY:'',
					USERNAME:this.addName,
					LAST_UPD_USR:'',
					LAST_UPD_DT:'',
					VER:''
	  			}
	  			console.log(JSON.stringify(resData))
	  			saveUserObjectPost(resData).then( res =>{
					this.bindingDialogVisible = false
					this.ruleForm.BO_KEY = ''
					this.addRightsData = []
//					window.localStorage.removeItem('boData')
	  				console.log('确定user关联Bo'+JSON.stringify(res))
	  			})
	  		},
	  		
	  		//编辑按钮
	  		editUserFun(obj){
	  			this.$router.push({
	  				name:'addEditUser01_list',
	  				params:{id:obj.id}
	  			})
	  		},
			//批量
			allDelBut(){
				this.delDialogObj.deldialogVisible = true;
				this.delDialogObj.isAll = true;
				
			},
			//删除单行
		    oneDeleteing(){
				this.delDialogObj.deldialogVisible = false
				deletePLUserPost({USER_ID:this.delId}).then( res =>{
					console.log('删除单行'+JSON.stringify(res))
					this.init()
				})
			},
			//多选删除确定
			batchDeleting(){
				var ids = this.multipleSelection.map(item => item.USER_ID).join();
				this.delDialogObj.deldialogVisible = false	
				console.log(JSON.stringify(ids))
				deletePLUserPost({USER_ID:ids}).then( res =>{
//					console.log('删除'+JSON.stringify(res))
					this.init()
				})
			},
			//单行删除按钮
			delFun(obj){
				this.delId=obj.id;
				this.delDialogObj.deldialogVisible = true				
			},
			//下拉角色
			selectRole(){
	  			queryRolePort().then( res =>{
	  				this.options = res.LISTROLEINFO
//	  				console.log('角色列表'+JSON.stringify(res))
	  			})
	  		},
	  		//点击选中事件触发  //bo绑定search
	  		handleSelect(item) {
		        console.log(item);
		   },
	       querySearchAsync(queryString, cb) {
	       		
		        var restaurants = this.restaurants;
		        var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
		        clearTimeout(this.timeout);
		        this.timeout = setTimeout(() => {
		           cb(results);
		        }, 1000 * Math.random());
		    },
	        createStateFilter(queryString) {
		        return (state) => {
		          return (state.BO_KEY.indexOf(queryString) === 0);
		        };
	    	},
			//table多选
		    handleSelectionChange(val) {
		        this.multipleSelection = val;
//		       console.log(JSON.stringify(val))
		    },
		    //初始页currentPage、初始每页数据数pagesize和数据data
	    	handleSizeChange: function (size) { 
				this.pagesize = size; 
			}, 
			handleCurrentChange: function(currentPage){ 
				this.currentPage = currentPage; 
				this.init();
			},
			//删除弹框关闭
		    handleClose(done,event) {
				this.$confirm('确认关闭？').then(() => {
					this.addRightsData = []
					done();
				}).catch(() => {});
			},
		}
  	}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scope">
	.user_wrap{
		.user-top{
			background: #fff;
			padding: 20px;
			border-radius: 6px;
		}
		.el-form-item__content{
			line-height: 10px;
		}
		.fontColor{
			font-size: 1.2rem;
			line-height:0;
			color: #999;
			font-style: normal;
		}
	}
</style>



