<template>
	<div>
		<el-row class = "content">
			<el-col>
				<el-table
				 	ref="multipleTable"
				    :data="tableData"
				    tooltip-effect="dark"
				    @selection-change = "selectCheckFun"
				    style="width: 100%"
					height="300">
				    <el-table-column width="55" type="selection">
				    </el-table-column>
				    <el-table-column label="素材标题" >
						<template slot-scope="scope">
							{{ scope.row.title }}
						</template>
				    </el-table-column>
				    <el-table-column  label="素材类型">
				    	<template slot-scope="scope">
				    		{{scope.row.type}}
				    	</template>
				    </el-table-column>
				    <el-table-column  label="创建人">
				    	<template slot-scope="scope">
				    		{{scope.row.creatorId}}
				    	</template>
				    </el-table-column>
				</el-table>
			</el-col>
			
		</el-row>
		
	</div>
</template>

<script>
//	import { materialListsPort } from  'api/curriculum'   //素材全部接口
	export default{
		props:[
			'option',
			'selectedList',
			'checkTable'
		],
		data(){
			return{
				value:this.option.id,
				input:'',
				index:0,
				selectOptionData:[{
					id:1,
					name:'tan01',
					disabled:false
				},{
					id:2,
					name:'tan02',
					disabled:false
				}],
				
				multipleSelection:'',
				tableData:[{
					id:'',
					title:'',
					type:'',
					name:''
				}],

			}
			
		},
		created(){
		},
		methods:{
			//改变checked触发
			selectCheckFun(val){
				this.multipleSelection = val;
				console.log(this.multipleSelection)
				this.$emit('multipleTable',{data:this.multipleSelection});
			},

		},
	}
</script>

<style lang="scss" slot-scope="scope">
	
</style>


